"use client"
import { useState, useRef } from 'react';

export default function Home() {
  const [text, setText] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const audioRef = useRef(null);

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!text.trim()) {
      setError('Please enter some text.');
      return;
    }

    setLoading(true);
    setError('');

    try {
      const response = await fetch('/api/text-to-speech', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ text }),
      });

      if (!response.ok) {
        const { error } = await response.json();
        throw new Error(error?.message || 'API Error');
      }

      const audioBlob = await response.blob();
      const audioUrl = URL.createObjectURL(audioBlob);

      // Set the audio source and play
      if (audioRef.current) {
        audioRef.current.src = audioUrl;
        audioRef.current.play();
      }

      setText(''); // Clear textbox
    } catch (err) {
      console.error('Error:', err);
      setError(err.message || 'Something went wrong');
    } finally {
      setLoading(false);
    }
  };

  return (
    <main className="flex flex-col items-center justify-center min-h-screen p-4 bg-gray-100">
      <h1 className="text-3xl font-bold mb-4">Affirmation to Voice</h1>
      <form onSubmit={handleSubmit} className="w-full max-w-md space-y-4">
        <textarea
          className="w-full p-3 border rounded shadow resize-none"
          rows={4}
          placeholder="Type your affirmation..."
          value={text}
          onChange={(e) => setText(e.target.value)}
        />
        <button
          type="submit"
          disabled={loading}
          className="w-full bg-blue-600 text-white py-2 rounded hover:bg-blue-700 transition"
        >
          {loading ? 'Generating...' : 'Speak'}
        </button>
        {error && <p className="text-red-500 text-sm">{error}</p>}
      </form>
      <audio ref={audioRef} className="mt-6" controls />
    </main>
  );
}
